$color1: #ccc;
$color2: #adeb4b;
$color3: #2b2b2b;
$color4: #3b3b3b;

* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
    Helvetica Neue, sans-serif;

  color: $color1;
}

body {
  background: $color3;
  max-width: 860px;
  width: 100%;
  margin: auto;

  .header {
    display: flex;
    background: $color4;
    margin-top: 30px;
    padding: 15px;

    @media (max-width: 470px) {
      flex-direction: column;
      justify-content: center;
      &__icons {
        justify-content: center;
      }
    }

    @media (max-width: 619px) {
      &__description {
        text-align: center;
      }
    }

    &__avatar {
      display: flex;
      margin: 0 20px;
      &-img {
        margin: auto;
        border-radius: 50%;
        border: 1px solid $color2;

        @media (max-width: 700px) {
          $size: 150px;
          height: $size;
          width: $size;
        }

        &:hover {
          border: 1px solid $color1;
        }
      }
    }

    &__icons {
      display: flex;
      justify-content: start;
      flex-wrap: wrap;

      @media (max-width: 619px) {
        & {
          max-width: 250px;
          justify-content: center;
          margin: auto;
        }
      }

      &-icon {
        margin-right: 10px;
        svg {
          $size: 2.5rem;
          height: $size;
          width: $size;
          color: $color2;
          &:hover {
            cursor: pointer;
            color: $color1;
          }
        }
      }
    }
  }

  .container {
    margin-top: 30px;
    margin-bottom: 30px;
    span {
      text-align: justify;
    }

    @media (max-width: 959px) {
      & {
        padding: 0 20px;
      }
    }
  }

  .footer {
    background: $color4;
    text-align: center;
    width: 100%;
    margin: 0 auto 30px;
    bottom: 0;
    padding: 10px;
  }
}
